.buttonWidget {
  transition: all 0.2s;
  border-radius: 10px;
  background: #ffffff;
  border: 1px solid #707070;
  box-shadow: none;
  color: #373742;
  cursor: pointer;
  display: inline-block;
  font-size: 1em;
  font-weight: 500;
  margin: 0;
  outline: none;
  overflow: visible;
  padding: 0.3em 1em;
  position: relative;
  text-align: center;
  text-shadow: none;
  zoom: 1;
}
.buttonWidget:hover:not(:disabled) {
  background: #373742;
  color: #ffffff;
}
.buttonWidget:disabled {
  opacity: 0.5;
  cursor: auto;
  background: #373742;
  color: #ffffff;
}
.buttonWidgetInline {
  cursor: pointer;
  padding: 0;
  background-color: transparent;
  color: inherit;
  border-radius: inherit;
}
.buttonWidgetInline:hover:not(:disabled) {
  background-color: transparent;
}
.buttonWidgetInline:disabled {
  opacity: 0.5;
  cursor: auto;
  background-color: transparent;
}